.v-stepper {
  border-radius: 4px;
  overflow: hidden;
  position: relative;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}
.v-stepper__header {
  height: 72px;
  align-items: stretch;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}
.v-stepper__header .v-divider {
  align-self: center;
  margin: 0 -16px;
}
.v-stepper__items {
  position: relative;
  overflow: hidden;
}
.v-stepper__step__step {
  align-items: center;
  border-radius: 50%;
  display: inline-flex;
  font-size: 0.75rem;
  justify-content: center;
  height: 24px;
  min-width: 24px;
  width: 24px;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.v-application--is-ltr .v-stepper__step__step {
  margin-right: 8px;
}
.v-application--is-rtl .v-stepper__step__step {
  margin-left: 8px;
}
.v-stepper__step__step .v-icon.v-icon {
  font-size: 1.25rem;
}
.v-stepper__step__step .v-icon.v-icon.v-icon--svg {
  height: 1.25rem;
  width: 1.25rem;
}
.v-stepper__step {
  align-items: center;
  display: flex;
  flex-direction: row;
  padding: 24px;
  position: relative;
}
.v-stepper__step--active .v-stepper__label {
  transition: 0.3s cubic-bezier(0.4, 0, 0.6, 1);
}
.v-stepper__step--editable {
  cursor: pointer;
}
.v-stepper__step.v-stepper__step--error .v-stepper__step__step {
  background: transparent;
  color: inherit;
}
.v-stepper__step.v-stepper__step--error .v-stepper__step__step .v-icon {
  font-size: 1.5rem;
  color: inherit;
}
.v-stepper__step.v-stepper__step--error .v-stepper__label {
  color: inherit;
  text-shadow: none;
  font-weight: 500;
}
.v-stepper__step.v-stepper__step--error .v-stepper__label small {
  color: inherit;
}
.v-stepper__label {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  line-height: 1;
}
.v-application--is-ltr .v-stepper__label {
  text-align: left;
}
.v-application--is-rtl .v-stepper__label {
  text-align: right;
}
.v-stepper__label small {
  font-size: 0.75rem;
  font-weight: 300;
  text-shadow: none;
}
.v-stepper__wrapper {
  overflow: hidden;
  transition: none;
}
.v-stepper__content {
  top: 0;
  padding: 24px 24px 16px 24px;
  flex: 1 0 auto;
  width: 100%;
}
.v-stepper__content > .v-btn {
  margin: 24px 8px 8px 0;
}
.v-stepper--is-booted .v-stepper__content, .v-stepper--is-booted .v-stepper__wrapper {
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
}
.v-stepper--vertical {
  padding-bottom: 36px;
}
.v-stepper--vertical .v-stepper__content {
  padding: 16px 60px 16px 23px;
  width: auto;
}
.v-application--is-ltr .v-stepper--vertical .v-stepper__content {
  margin: -8px -36px -16px 36px;
}
.v-application--is-rtl .v-stepper--vertical .v-stepper__content {
  margin: -8px 36px -16px -36px;
}
.v-stepper--vertical .v-stepper__step {
  padding: 24px 24px 16px;
}
.v-application--is-ltr .v-stepper--vertical .v-stepper__step__step {
  margin-right: 12px;
}
.v-application--is-rtl .v-stepper--vertical .v-stepper__step__step {
  margin-left: 12px;
}
.v-stepper--alt-labels .v-stepper__header {
  height: auto;
}
.v-stepper--alt-labels .v-stepper__header .v-divider {
  margin: 35px -67px 0;
  align-self: flex-start;
}
.v-stepper--alt-labels .v-stepper__step {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-basis: 175px;
}
.v-stepper--alt-labels .v-stepper__step small {
  align-self: center;
}
.v-stepper--alt-labels .v-stepper__step__step {
  margin-bottom: 11px;
  margin-left: 0;
  margin-right: 0;
}

@media only screen and (max-width: 959px) {
  .v-stepper:not(.v-stepper--vertical) .v-stepper__label {
    display: none;
  }
  .v-stepper:not(.v-stepper--vertical) .v-stepper__step__step {
    margin-left: 0;
    margin-right: 0;
  }
}