.v-rating {
  max-width: 100%;
  white-space: nowrap;
}
.v-rating .v-icon {
  padding: 0.5rem;
  border-radius: 50%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
}
.v-rating .v-icon::after {
  display: none;
}
.v-application--is-ltr .v-rating .v-icon {
  transform: scaleX(1);
}
.v-application--is-rtl .v-rating .v-icon {
  transform: scaleX(-1);
}
.v-rating--readonly .v-icon {
  pointer-events: none;
}
.v-rating--dense .v-icon {
  padding: 0.1rem;
}