.v-data-table {
  max-width: 100%;
}
.v-data-table table {
  width: 100%;
  border-spacing: 0;
}
.v-data-table td, .v-data-table th {
  padding: 0 16px;
}
.v-data-table th {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 0.75rem;
  height: 48px;
}
.v-application--is-ltr .v-data-table th {
